<script>
import { Button } from "iview";
import { CountUp } from "countup.js";
import { imgUrl } from "../../utils";
import {
  getMoneyIndustryList,
  getMoneyQuality,
  getMoneyNewListy,
  getMoneySummary
} from "../../api/public";
export default {
  components: {
    [Button.name]: Button
  },
  data() {
    return {
      imgUrl: imgUrl,
      moneyIndustryList: [], //获取行业精选资金
      moneyQuality: [], //获取精选资金
      moneyNewListy: [], //获取最新资金
      moneySummary: [] //获取资金主页信息
    };
  },
  methods: {
    //	获取行业精选资金
    getMoneyIndustryList() {
      getMoneyIndustryList({ size: 8 }).then(res => {
        this.moneyIndustryList = res.data.data;
      });
    },
    //获取精选资金
    getMoneyQuality() {
      getMoneyQuality({ size: 6 }).then(res => {
        this.moneyQuality = res.data.data;
      });
    },
    //获取最新资金
    getMoneyNewListy() {
      getMoneyNewListy({ size: 6 }).then(res => {
        this.moneyNewListy = res.data.data;
      });
    },
    //获取最新资金
    getMoneySummary() {
      getMoneySummary().then(res => {
        this.moneySummary = res.data.data;
        let numberUp1 = new CountUp("numberUp1", this.moneySummary.moneySide);
        let numberUp2 = new CountUp("numberUp2", this.moneySummary.moneySum);
        let numberUp3 = new CountUp(
          "numberUp3",
          this.moneySummary.moneyTrading
        );
        let numberUp4 = new CountUp(
          "numberUp4",
          this.moneySummary.moneyDocking
        );
        numberUp1.start() ||
          numberUp2.start() ||
          numberUp3.start() ||
          numberUp4.start();
      });
    }
  },
  created() {
    this.getMoneyIndustryList();
    this.getMoneyQuality();
    this.getMoneyNewListy();
    this.getMoneySummary();
  }
};
</script>
